.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;
	background-color: #fff;
	padding-bottom: 10px;
	box-shadow: 0 4px 4px rgba(#000, .2);

	&__top {
		margin-bottom: 10px;
		padding-top: 10px;
		padding-bottom: 10px;
		background-color: #B83020;
	}

	&__top-nav {
		display: flex;
		justify-content: flex-end;
	}

	&__top-el {
		position: relative;
		padding: 0 13px;

		&:first-child {
			padding-left: 0;
		}

		&:last-child {
			padding-right: 0;
		}

		&:not(:last-child) {
			&::after {
				position: absolute;
				top: 50%;
				right: 0;
				transform: translateY(-50%);
				height: 14px;
				border-right: 1px solid #fff;
				content: "";
			}
		}
	}

	&__top-link {
		font-family: $font-secondary;
		font-size: 12px;
		line-height: 14px;
		color: #fff;

		&:hover {
			text-decoration: underline;
		}
	}

	&__content {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__logo-box {
		display: inline-block;
	}

	&__logo {
		display: block;
	}

	&__nav-box {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 40px;
	}

	&__menu-btn {
		display: inline-flex;
		align-items: center;
		gap: 12px;
		padding: 13px 30px;
		background-color: #3D4B88;
		border-radius: 100px;
		border: none;
		font-family: $font-secondary;
		font-size: 14px;
		line-height: 16px;
		font-weight: 600;
		color: #fff;
		white-space: nowrap;
		transition: background-color $transition-default;

		&:hover {
			background-color: darken(#3D4B88, 10%);
		}
	}

	&__menu-icon-box {
		flex-shrink: 0;
		position: relative;
		width: 24px;
		height: 24px;
	}

	&__menu-icon {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		transition: opacity $transition-default;

		&--normal {
			.header--menu-shown & {
				opacity: 0;
			}
		}

		&--active {
			opacity: 0;

			.header--menu-shown & {
				opacity: 1;
			}
		}
	}

	&__nav {
	}

	&__nav-list {
		display: flex;
		align-items: center;
		gap: 40px;
	}

	&__nav-el {
	}

	&__nav-link {
		font-family: $font-secondary;
		font-size: 14px;
		line-height: 16px;

		&:hover {
			text-decoration: underline;
		}
	}

	&__action-list {
		display: flex;
		align-items: center;
		gap: 10px;
	}

	&__action-el {
		flex-shrink: 0;
	}

	&__action-link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 50px;
		height: 50px;
		border-radius: 50px;
		transition: background-color $transition-default;

		&:hover {
			background-color: #E7EDF7
		}
	}

	&__action-icon {
		display: block;
		width: 30px;
		height: 30px;
	}

	// Menu
	&__menu {
		display: none;
		position: absolute;
		z-index: -1;
		top: 0;
		left: 0;
		right: 0;
		height: 100vh;
		padding-top: 119px;
		background-color: #fff;

		.header--menu-shown & {
			display: block;
		}
	}

	&__menu-container {
		padding-top: 30px;
		padding-bottom: 30px;
	}

	&__menu-top {
		background-color: #F3F3F3;
		padding: 10px;
		border-radius: 10px;
		margin-bottom: 40px;
	}

	&__menu-top-slider {
	}

	&__menu-top-el {
		padding: 10px 12px;
		border-radius: 6px;
		cursor: pointer;
		text-align: center;
		transition: background-color $transition-default, color $transition-default;

		&--active,
		&:hover {
			background-color: #3D4B88;
			color: #fff;
		}
	}

	&__menu-content-box {
		display: none;

		&--active {
			display: block;
		}
	}

	&__menu-title-box {
		display: flex;
		gap: 30px;
		margin-bottom: 46px;
	}

	&__menu-title {
		margin-bottom: 0;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;

	}

	&__menu-title-link {
		font-size: 16px;
		height: 20px;
		color: #3D4B88;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;

		&:hover {
			text-decoration: underline;
		}
	}

	&__menu-links-list {
		display: grid;
		grid-template: auto / repeat(4, minmax(0,1fr));
		grid-gaP: 20px;
	}

	&__menu-link {
		display: flex;
		align-items: center;
		padding: 10px;
		border-radius: 6px;
		font-size: 14px;
		line-height: 17px;
		transition: box-shadow $transition-default;

		&--active,
		&:hover {
			@include main-shadow;
		}
	}
}
